import React, { Component } from 'react'
import { Empty } from 'antd'

class Front extends Component {
    render() {
        const EmptyStyle = {
            height: '873px', padding: '10% 0', margin: '0 0 30px 0', backgtound: '#fff'
        };
        return (
            <div>
                <Empty image={Empty.PRESENTED_IMAGE_SIMPLE} style={EmptyStyle} />
            </div >
        )
    }
}

export default Front;