import { INIT_ARTICLE_DATA } from './actionTypes';

const defaultState = {
    list: []
}

//reducer可以接收state，但是绝不能修改state
export default (state = defaultState, action) => {
    if (action.type === INIT_ARTICLE_DATA) {
        const newState = JSON.parse(JSON.stringify(state));
        newState.list = action.data;
        return newState;
    }
    //state:整个store仓库里存储的数据
    return state;
}