import { takeEvery, put } from 'redux-saga/effects';
import { GET_ARTICLE_DATA } from '../actionTypes';
import { initArticleData } from '../actionCreators';
import axios from 'axios';

function* getArticleData() {
    try {
        const res = yield axios.get('http://localhost:4000/data');
        const action = initArticleData(res.data.data);
        yield put(action);
        console.log("成功")
    } catch (e) {
        console.log('网络请求失败')
    }
}

//generator 函数
function* frontSaga() {
    yield takeEvery(GET_ARTICLE_DATA, getArticleData);
}

export default frontSaga;