import React, { Component } from 'react';
import { Carousel } from 'antd';
import 'antd/dist/antd.css'
import './banner.scss'

class Banner extends Component {
    render() {
        return (
            <Carousel autoplay>
                <div>
                    <h1 className='key_words'>S_JM</h1>
                    <p className='welcome_text'>Welcome to my blog</p>
                </div>
            </Carousel>
        )
    }
}

export default Banner;