import React, { Component } from 'react'
import { Layout, Row, Col, Menu, Icon } from 'antd'
import 'antd/dist/antd.css'
import './menus.scss'
import { Link } from 'react-router-dom';

const { Header } = Layout;
const { SubMenu } = Menu;

class Menus extends Component {
    state = {
        current: 'home',
    };

    handleClick = e => {
        console.log('click ', e);
        this.setState({
            current: e.key,
        });
    };

    render() {
        const menus = (
            <Menu
                onClick={this.handleClick}
                selectedKeys={[this.state.current]}
                mode="horizontal"
                theme='dark'
                overflowedIndicator={<Icon type="bars" style={{ fontSize: '24px', lineHeight: '66px' }} />}
            >
                <Menu.Item key="home">
                    <Link to="/" style={this.state.current == "home" ? { color: '#61dafb' } : { color: '#fff' }}>
                        <Icon type="home" style={{ fontSize: '16px' }} />
                        首页
                    </Link>
                </Menu.Item>
                <SubMenu
                    title={
                        <span className="submenu-title-wrapper">
                            <Icon type="mail" style={{ fontSize: '16px' }} />
                            文章
                    </span>
                    }
                >
                    <Menu.ItemGroup title="技术交流">
                        <Menu.Item key="setting:1">
                            <Link to="/notdata">JavaScript</Link>
                        </Menu.Item>
                        <Menu.Item key="setting:2">
                            <Link to="/notdata">React</Link>
                        </Menu.Item>
                    </Menu.ItemGroup>
                    <Menu.ItemGroup title="生活">
                        <Menu.Item key="setting:3">
                            <Link to="/notdata">杂谈</Link>
                        </Menu.Item>
                        <Menu.Item key="setting:4">
                            <Link to="/notdata">趣事</Link>
                        </Menu.Item>
                    </Menu.ItemGroup>
                </SubMenu>
                <SubMenu
                    title={
                        <span className="submenu-title-wrapper">
                            <Icon type="link" style={{ fontSize: '16px' }} />
                            友链
                    </span>
                    }
                >
                    <Menu.Item key="setting:11">
                        <a href='https://www.idrlzt.cn/'>Simba</a>
                    </Menu.Item>
                    <Menu.Item key="setting:12">
                        <a href='https://www.lexblog.cn/'>Lex</a>
                    </Menu.Item>
                    <Menu.Item key="setting:13">
                        <a href='https://kangna.moe/'>Kangna</a>
                    </Menu.Item>
                </SubMenu>
                <Menu.Item key="app" disabled>
                    <Icon type="appstore" style={{ fontSize: '16px' }} />
                    待开发
            </Menu.Item>
            </Menu >
        )
        return (
            <div className="header">
                <Header>
                    <Row>
                        <Col className="Col_logo" span={2} offset={4}>
                            <div className="logo">S_JM</div>
                        </Col>
                        <Col span={8} offset={2}>
                            {menus}
                        </Col>
                    </Row>
                </Header>
            </div>
        );
    }
}

export default Menus;