import React, { Component } from 'react'
import { List, Card, Col, Row, Tag } from 'antd'
import './card.scss'

const Article_card = (props) => {
    var tag;
    return (
        <List
            style={{}}
            dataSource={props.list}
            renderItem={
                (item, index) => (
                    <Card
                        hoverable
                        title={item.Title}
                        extra={<a href="#">More</a>}
                        key={props.list[index].aid}
                    >
                        {console.log(props.list)}
                        <div className='cards_description'>
                            {item.Description}
                        </div>
                        <div className='cards_tag'>
                            <List
                                style={{}}
                                dataSource={tag = item.Tag.split(",")}
                                renderItem={(tag_item, index) => (
                                    <Tag color="#2db7f5">
                                        {tag_item}
                                    </Tag>
                                )}
                            >
                            </List>
                        </div>
                        <div className='cards_times'>
                            {item.Time}
                        </div>
                    </Card>
                )
            }
        />
    )
}

export default Article_card;